import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '../shared/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-qr-login',
  templateUrl: './qr-login.component.html',
  styleUrls: ['./qr-login.component.css']
})
export class QrLoginComponent implements OnInit {
  checkInterval: any;
  loginToken: any;
  timestamp: string;

  JSON = JSON;
  constructor(
    private auth: AuthService,
    private _flash: FlashMessagesService,
    private router: Router
  ) { }

  ngOnInit() {
    this.generateQr();
    this.checkInterval = setInterval(() => {
      if(this.loginToken) {
        this.auth.checkQr(this.loginToken._id).subscribe( res => {
          if(res.success) {
            this.auth.storeUserData(res.user, res.authToken, true);
            this._flash.show('Login successful!', {cssClass: 'alert-success', timeout: 3000});
            this.router.navigate(['session-qr']);
          } else {
            //
          }
        }, err => {
          console.log(err);
          this._flash.show('Server error', {cssClass: 'alert-danger', timeout: 3000});
        })
      }
    }, 3000);
  }

  ngOnDestroy() {
    if(this.checkInterval) {
      clearInterval(this.checkInterval);
    }
    this.auth.removeAllTokens(this.timestamp).subscribe(res => {
      //
    })
  }

  generateQr() {
    this.timestamp = Date.now().toString();
    this.auth.generateQr(this.timestamp).subscribe( res => {
      if(res.success) {
        this.loginToken = res.token;
      }
    })
  }

}



