import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { AuthService } from '../shared/auth.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  captchaKey: string = '6LevrjQUAAAAAM5WB0Xu_ttsNRqpXeSPV6F0_zek';

  logIn: FormGroup = new FormGroup({
    username: new FormControl(null, Validators.required),
    password: new FormControl(null, Validators.required),
    captcha: new FormControl()
  });

  constructor(private authService: AuthService,
              private router: Router) { }

  ngOnInit() {
  }
  onLogin(){
    if(!this.logIn.valid) return; //show error;
    let username = this.logIn.value.username;
    let password = this.logIn.value.password;
    this.authService.authenticateUser(username, password)

    .subscribe(data => {
    if(data.success){
      console.log(data);
      this.authService.storeUserData(data.user, data.token);
      if(data.user.username === "Admin") {
        this.router.navigate(['admin']);
        return;
      }
      this.router.navigate(['gallery']);

    }
    else{
      console.log(data);
      this.router.navigate(['login']);
    }

    });


  }

}



