import { Component } from '@angular/core';
import { DialogComponent, DialogService } from "ng2-bootstrap-modal";
export interface PopupModal {
  title:string;
  message:string;
  filename: string;
  delay: number;
  imgTitle: string;
}

@Component({
    selector: 'popup',
    styleUrls: ['./popup.component.css'],
    template: `<div class="modal-dialog">
                <div class="modal-content">
                   <div class="modal-header">
                     <button type="button" class="close" (click)="close()" >&times;</button>
                     <h4 class="modal-title">{{title || 'Confirm'}}</h4>
                   </div>
                   <div class="modal-body">
                     <p>{{message || 'Enter image delay and title for'+filename}}</p>
                     <input class="form-control" placeholder="Delay (in seconds)" type="number" [(ngModel)]="delay"/>
                     <br>
                     <input class="form-control" placeholder="Title" type="text" [(ngModel)]="imgTitle"/>
                   </div>
                   <div class="modal-footer">
                     <button type="button" class="btn btn-primary" (click)="confirm()">OK</button>
                     <button type="button" class="btn btn-default" (click)="close()" >Cancel</button>
                   </div>
                 </div>
              </div>`
})
export class PopupComponent extends DialogComponent<PopupModal, any> implements PopupModal {
  title: string;
  message: string;
  filename: string;
  delay: number;
  imgTitle: string;
  constructor(dialogService: DialogService) {
    super(dialogService);
  }
  confirm() {
    this.result = {
      delay: this.delay,
      title: this.imgTitle
    }
    this.close();
  }
}



