import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { ImageService } from '../shared/image.service';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'session-qr',
  templateUrl: './session-qr.component.html',
  styleUrls: ['./session-qr.component.css']
})
export class SessionQrComponent implements OnInit {

  constructor(private imageService: ImageService,
      private _flash: FlashMessagesService,
      private router: Router) { }
  sessions: any[] = [];
  qrCodes: any[] = [];
  checkInterval:any;
  JSON = JSON;

  ngOnInit() {
    this.populateSessions();
    this.checkInterval = setInterval(() => {
      this.imageService.checkQr().subscribe( res => {
        if(res.success) {
          this.startSlideShow(res.session);
        }
      })
    }, 3000);
  }

  ngOnDestroy() {
    if(this.checkInterval) {
      clearInterval(this.checkInterval);
    }
  }

  populateSessions() {
    this.imageService.getAllSessions().subscribe( res => {
      if(res.success) {
        this.sessions = res.data;
        this.sessions.forEach((value, index) => {
          this.qrCodes.push({user: value.userId, session: value._id, sessionname: value.sessionname});
        });
      } else {
        this._flash.show('There was an error', {cssClass: 'alert-danger', timeout: 3000})
      }
    }, err => {
      this._flash.show('Server error', {cssClass: 'alert-danger', timeout: 3000})
    })
  }

  startSlideShow(session) {
    this.router.navigate(['/slideshow', session._id]);
  }



}



