import { Component } from '@angular/core';
import { CategoryService } from '../shared/category.service';
import { AuthService } from '../shared/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
    moduleId: module.id,
    selector: 'admin',
    templateUrl: 'admin.component.html',
    styleUrls: ['admin.component.scss']
})
export class AdminComponent {
    title = 'Manage Categories';
    currentUser = this.auth.getCurrentUser().username;
    categoryname: String;
    subcategoryname: String;
    oldcategoryname: String;
    categories: any[] = [];
    rootCategories: any[] = [];
    subCategories: any[] = [];
    pathCategories: any[] = [];

    // mailing service credentials
    smtpUser: string;
    smtpPass: string;
    smtpMailer: string;

    constructor(private categoryService: CategoryService, private _flash: FlashMessagesService, private auth: AuthService) {
        this.categoryService.getRootCategory()
            .subscribe(data => {
                if (data.success) {
                    this.categories = data.categories;
                    this.rootCategories = data.categories;
                    console.log(this.categories);
                }
            });
    }

    createNewCategory() {
        let cName = this.categoryname
        this.categoryService.addRootCategory(cName)

            .subscribe(data => {
                if (data.success) {
                  console.log(data.category);
                    this.categoryname = null;
                    this.categories.push(data.category);
                    this.rootCategories = this.categories;
                    console.log(this.categories);
                }
            });
    };

    createNewSubCategory(category) {
        let subCName = category.subcategoryname;
        let parent = category.categoryname
        let parentId = category._id;
        this.categoryService.addSubCategory(parentId, parent, subCName)

            .subscribe(data => {
                if (data.success) {
                    category.subcategoryname = null;
                }
            });
    };

    getSubCategories(category) {
        let id = category._id;
        this.categories = [];
        this.populatePath(category);
        this.categoryService.getSubCategory(id)

            .subscribe(data => {
                if (data.success) {
                    this.subCategories = data.category.childcategories;
                    this.categories = this.subCategories;
                }
            });
    };

    removeCategory(ct, index) {
        if (ct) {
            let categoryname = ct.categoryname;
            let id = ct._id;
            this.categoryService.removeCategory(categoryname, id)

                .subscribe(data => {
                    if (data.success) {
                        this.categories.splice(index, 1);
                        console.log('Deleted Successfully');
                    }
                });
        }
    }

    toggleUpdate(ct, index) {
        if (ct.isEdit) {
            this.categories[index].isEdit = false;
            this.oldcategoryname = "";
        } else {
            this.categories[index].isEdit = true;
            this.oldcategoryname = ct.categoryname
        }
    }

    editCategory(ct, index) {
        if (ct) {
            let category = { newcategoryname: ct.categoryname, oldcategoryname: this.oldcategoryname };
            let id = ct._id;
            this.categoryService.updateCategory(category, id)

                .subscribe(data => {
                    if (data.success) {
                        this.categories[index] = ct.categoryname;
                        ct.isEdit = false;
                        this.oldcategoryname = "";
                        console.log('Updated Successfully');
                    }
                });
        }
    }

    populatePath(ct) {
        let selectedCategory = this.pathCategories.find(item => item._id === ct._id);
        if (selectedCategory) {
            let index = this.pathCategories.findIndex(item => item._id === ct._id);
            this.pathCategories.splice(index + 1, this.pathCategories.length - index);
        } else {
            this.pathCategories.push(ct);
        }
    }

    populateRootCategories() {
        this.categories = this.rootCategories;
        this.pathCategories = [];
    }

    storeMailingCredentials () {
      let credentials = {
        smtpUser: this.smtpUser,
        smtpPass: this.smtpPass,
        smtpMailer: this.smtpMailer
      }
      this.categoryService.storeMailing(credentials).subscribe( res => {
        if(res.success){
          this._flash.show('SMTP mailing credentials saved!', {cssClass: 'alert-success', timeout: 2000});
        } else {
          this._flash.show('Error occured!', {cssClass: 'alert-danger', timeout: 2000});
        }
      })
    }
}



