import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { JsonPipe } from '@angular/common';
import { ImageService } from '../shared/image.service';
import { CategoryService } from '../shared/category.service';
import { Observable } from 'rxjs/Rx';
import { DndModule } from 'ng2-dnd';
import { QRCodeComponent } from 'angular2-qrcode';
import { AuthService } from '../shared/auth.service';

@Component({
  selector: 'app-gallery',
  templateUrl: './gallery.component.html',
  styleUrls: ['./gallery.component.css']
})
export class GalleryComponent implements OnInit {

  title = 'Recent Media Files';
  currentUser = this.auth.getCurrentUser().username;
  // visibleImages: any[] = [];
  totalSessions: any[];
  catones: any[] = [];
  cattwos: any[] = [];
  catthrees: any[] = [];
  JSON = JSON;
  //qr codes
  qrCodes: any[] = [];

  constructor(private imageService: ImageService, public router: Router,
    private categoryService: CategoryService,
    private auth: AuthService) {
    this.categoryService.getRootCategory()
      .subscribe(data => {
        if (data.success) {
          this.catones = data.categories;
        }
      });
    this.populateSessions(null);
  }

  populateSubCategoriesII(id) {
    this.cattwos = [];
    this.catthrees = [];
    this.totalSessions = [];
    if (id) {
      this.categoryService.getSubCategory(id)
        .subscribe(data => {
          if (data) {
            this.cattwos = data.category? data.category.childcategories: [];
          }
        });
      this.populateSessions(id, true);
    }
  }

  populateSubCategoriesIII(id) {
    this.catthrees = [];
    this.totalSessions = [];
    if (id) {
      this.categoryService.getSubCategory(id)
        .subscribe(data => {
          if (data.success) {
            this.catthrees = data.category? data.category.childcategories: [];
          }
        });
      this.populateSessions(id, true);
    }
  }

  populateSessions(categoryId, isParent?) {
    this.totalSessions = [];
    if (categoryId) {
      this.imageService.getSessions(categoryId, isParent)
        .subscribe(data => {
          if (data.success) {
            this.totalSessions = data.data;
            this.totalSessions.forEach((value, index) => {
              this.qrCodes.push({user: value.userId, session: value._id, sessionname: value.sessionname});
            })
          }
        });
    }
  }

  showImage(session) {
    if (session.images) {
      session.images = null;
      return;
    }
    this.imageService.getImages(session._id)
      .subscribe(data => {
        if (data.success) {
          session.images = data.files;
        }
      });
  }

  dragEnd(images) {
    let items = images;
    for (var index = 0; index < items.length; index++) {
      var element = items[index];
      this.imageService.updateImage(element._id, index)
        .subscribe(data => {
          if (data.success) {
            console.log('Suffeling Successful');
          }
        });
    }

  }

  startSlideShow(session) {
    this.router.navigate(['/slideshow', session._id]);
  }

  /*
  startSlideShow(session) {
    this.api.fsAPI.toggleFullscreen();

    if (session.images) {
      this.visibleImages = session.images;
      this.slideshow();
    } else {
      this.imageService.getImages(session.sessionname)
        .subscribe(images => {
          if (images.length) {
            this.visibleImages = images;
            this.slideshow();
          }
        });
    }
  }*/

  public checkInterval;
  ngOnInit() {
    // this.imageService.createQrCodes().subscribe(res => {
    //   if(res.success) {
    //     this.qrCodes = res.data;
    //     console.log(this.qrCodes);
    //   } else {
    //     console.log(res.error);
    //   }
    // })

    this.checkInterval = setInterval(() => {
      this.imageService.checkQr().subscribe( res => {
        if(res.success) {
          this.startSlideShow(res.session);
        }
      })
    }, 3000);
  }

  ngOnDestroy() {
    // this.imageService.removeAllTokens().subscribe( res => {
    //   // callbacks
    // })
    if(this.checkInterval) {
      clearInterval(this.checkInterval);
    }
  }

}



