import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { HttpModule } from '@angular/http';
import { FileSelectDirective } from 'ng2-file-upload';
import { RouterModule, Routes } from '@angular/router';
import { AlertModule } from 'ngx-bootstrap';
import { VgCoreModule } from 'videogular2/core';
import { DndModule } from 'ng2-dnd';
import { QRCodeModule } from 'angular2-qrcode';

import { BootstrapModalModule } from 'ng2-bootstrap-modal';

import {IonCaptchaDirective} from './shared/ionCaptcha.directive';

import {FlashMessagesModule} from 'angular2-flash-messages';

import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { SlideshowComponent } from './slideshow/slideshow.component';
import { HeaderComponent } from './header/header.component';
import { GalleryComponent } from './gallery/gallery.component';
import { AdminComponent } from './admin/admin.component';
import { ImageComponent } from './image/image.component';
import { ImageService } from './shared/image.service';
import { CategoryService } from './shared/category.service';
import { AuthService } from './shared/auth.service';
import { UploadComponent } from './upload/upload.component';
import { AuthGuard } from './shared/auth.guard';
import { RegisterComponent } from './register/register.component';
import { PopupComponent } from './popup/popup.component';
import { SessionQrComponent } from './session-qr/session-qr.component';
import { QrLoginComponent } from './qr-login/qr-login.component';

const ROUTES = [
  {
    path: '',
    redirectTo: 'login',
    pathMatch: 'full'
  },
  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: 'qr-login',
    component: QrLoginComponent
  },
  {
    path: 'register',
    component: RegisterComponent
  },
  {
    path: 'images',
    component: ImageComponent
  },
  {
    path: 'gallery',
    component: GalleryComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'slideshow/:id',
    component: SlideshowComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'session-qr',
    component: SessionQrComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'upload',
    component: UploadComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'admin',
    component: AdminComponent,
    canActivate: [AuthGuard]
  }

];

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    HeaderComponent,
    GalleryComponent,
    SlideshowComponent,
    ImageComponent,
    UploadComponent,
    AdminComponent,
    FileSelectDirective,
    RegisterComponent,
    PopupComponent,
    IonCaptchaDirective,
    SessionQrComponent,
    QrLoginComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    VgCoreModule,
    QRCodeModule,
    AlertModule.forRoot(),
    RouterModule.forRoot(ROUTES),
    DndModule.forRoot(),
    FlashMessagesModule,
    BootstrapModalModule.forRoot({container:document.body})
  ],
  entryComponents: [
    PopupComponent
  ],
  providers: [ImageService, AuthService, CategoryService, AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }



