import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import {environment} from '../../environments/environment';

@Injectable()
export class CategoryService {
  headers: Headers = new Headers();
  constructor(private http: Http) {
    this.headers.append('Authorization', localStorage.getItem('id_token'));
    this.headers.append('Content-Type', 'application/json');
  }

  visibleImages = [];

  // private splashUrl = 'http://www.splashbase.co/api/v1/images/random';
  // private baseUrl = 'http://localhost:3000/api/';

  // private baseUrl = 'http://localhost:8080/category/';
  // private baseUrl = 'https://ionic-node-auth.herokuapp.com/';

  private baseUrl = environment.baseUrl;

  getRootCategory() {
    return this.http.get(this.baseUrl + 'category/getrootcategories', {headers: this.headers})
      .map(res => res.json());
  }

  getSubCategory(id) {
    let data = { id: id };
    return this.http.post(this.baseUrl + 'category/getsubcategories', JSON.stringify(data), { headers: this.headers })
      .map(
      res => res.json(),
      err => console.log(err)
      );
  }

  addSubCategory(parentId, parentcategory, categoryname) {
    let data = { parentcategory: parentcategory, categoryname: categoryname, parentId: parentId };
    return this.http.post(this.baseUrl + 'category/addsubcategory', JSON.stringify(data), { headers: this.headers })
      .map(
      res => res.json(),
      err => console.log(err)
      );
  }

  addRootCategory(categoryname) {
    let data = { categoryname: categoryname };
    return this.http.post(this.baseUrl + 'category/addrootcategory', JSON.stringify(data), { headers: this.headers })
      .map(
      res => res.json(),
      err => console.log(err)
      );
  }

  removeCategory(categoryname, id) {
    let data = { id: id, categoryname: categoryname };
    return this.http.post(this.baseUrl + 'category/deletecategory', JSON.stringify(data), { headers: this.headers })
      .map(
      res => res.json(),
      err => console.log(err)
      );
  }

  updateCategory(category, id) {
    let data = { id: id, category: category };
    return this.http.post(this.baseUrl + 'category/updatecategory', JSON.stringify(data), { headers: this.headers })
      .map(
      res => res.json(),
      err => console.log(err)
      );
  }

  storeMailing(credentials) {
    return this.http.post(this.baseUrl + 'admin/storemailer', credentials, {headers: this.headers})
      .map(
        res => res.json(),
        err => console.log(err)
      );
  }

//   removeImage(id) {
//     let data = localStorage.getItem('uname');
//     return this.http.post(this.removeImageUrl, { username: data, id: id })
//       .map(res => res.json());
//   }

//   updateImage(id, index) {
//     return this.http.post(this.updateImageUrl, { index: index, id: id })
//       .map(res => res.json());
//   }


  // Static Images
  // getImages(id: number){
  // 	return IMAGES.slice(0).find(image => image.id == id);
  // }

  // randomImage() {
  //   return this.http.get(this.splashUrl)
  //     .map(res => res.json());

  // }

}

