import { Component, OnInit } from '@angular/core';
import { FileUploader, FileUploaderOptions } from 'ng2-file-upload';
import { ImageService } from '../shared/image.service';
import { CategoryService } from '../shared/category.service';

import { DialogService } from "ng2-bootstrap-modal";
import { PopupComponent } from '../popup/popup.component';

import {environment} from '../../environments/environment';

@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.css']
})
export class UploadComponent implements OnInit {
  // baseUrl: string = 'http://localhost:8080/';
  // baseUrl: string = 'https://ionic-node-auth.herokuapp.com/';
  baseUrl: string = environment.baseUrl;
  uploadUrl: string = this.baseUrl + 'session/upload';
  sessionname: String;
  totalSessions: any[] = [];
  uploadImage: null;
  catones: any[] = [];
  cattwos: any[] = [];
  catthrees: any[] = [];

  public uploader: FileUploader = new FileUploader({ url: this.uploadUrl });

  constructor(private imageService: ImageService, private categoryService: CategoryService, private dialogService: DialogService) {
    this.categoryService.getRootCategory()
      .subscribe(data => {
        if (data.success) {
          this.catones = data.categories;
        }
      });

    this.populateSessions(null);
  }

  onOnInit() {
  }

  populateSubCategoriesII(id) {
    this.cattwos = [];
    this.catthrees = [];
    this.totalSessions = [];
    if (id) {
      this.categoryService.getSubCategory(id)
        .subscribe(data => {
          if (data) {
            this.cattwos = data.category? data.category.childcategories: [];
          }
        });
      this.populateSessions(id, true);
    }
  }

  populateSubCategoriesIII(id) {
    this.catthrees = [];
    this.totalSessions = [];
    if (id) {
      this.categoryService.getSubCategory(id)
        .subscribe(data => {
          if (data) {
            this.catthrees = data.category? data.category.childcategories: [];
          }
        });
      this.populateSessions(id, true);
    }
  }

  populateSessions(categoryId, isParent?) {
    this.totalSessions = [];
    if (categoryId) {
      this.imageService.getSessions(categoryId, isParent)
        .subscribe(data => {
          if (data.success) {
            this.totalSessions = data.data;
          }
        });
    }
  }

  addImage(session) {
    this.uploadImage = session.sessionname;
    var uo: FileUploaderOptions = {};
    uo.additionalParameter = { sessionname: this.uploadImage, sessionId: session._id };
    uo.itemAlias = 'upload_file';
    uo.headers = [{name: 'Authorization', value: localStorage.getItem('id_token')}];
    this.uploader.setOptions(uo);

    this.uploader.onSuccessItem = (item, response, status, headers) => {
      if(item.file.type.indexOf('image/') !== -1) {
        console.log('i am herer!');
        let delaySub = this.showDelayPopup(item.file.name).subscribe(data => {
          console.log(data);
          if(data && data.delay) {
            this.imageService.updateDelay(JSON.parse(response).file._id, data.delay).subscribe( res => {
              if(res.success) {
                //success
              } else {
                 //error
              }
            }, error => {
              //http error
            })
          }
          if( data && data.title) {
            this.imageService.updateTitle(JSON.parse(response).file._id, data.title).subscribe(res => {
              if(res.success) {
                //success
              } else {
                //error
              }
            }, error => {
              //http error
            })
          }
        })
      }
    }
  }

  removeImage(image, images) {
    let id = image._id;
    this.imageService.removeImage(id)
      .subscribe(data => {
        if (data.success) {
          images.splice(images.indexOf(image), 1);
          console.log('Removed Successfully')
        }
      });
  }

  showImage(session) {
    this.imageService.getImages(session._id)
      .subscribe(data => {
        if (data.success) {
          session.images = data.files;
          console.log(session.images);
        }
      });
  }

  createNewSession(categoryId) {
    let sessionname = this.sessionname;
    let category = this.catthrees.find(function (ele) {
      return ele._id === categoryId
    });
    let categoryname = category.categoryname;
    if (categoryname) {
      this.imageService.createSessions(sessionname, categoryname, categoryId)

        .subscribe(data => {
          if (data.success) {
            this.sessionname = null;
            this.totalSessions.push(data.session);
          }
        });
    }
  };

  showDelayPopup(filename) {
      let disposable = this.dialogService.addDialog(PopupComponent, {
          title:'Enter delay for image',
          message:'Enter Delay for image: '+filename})

      return disposable;
  }


  ngOnInit() {
  }

}



