import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { AuthService } from './auth.service';

import {environment} from '../../environments/environment';

@Injectable()
export class ImageService {


  visibleImages = [];
  headers: Headers = new Headers();
  // baseUrl: string = 'http://localhost:8080/';
// baseUrl: string = 'https://ionic-node-auth.herokuapp.com/';
  baseUrl: string = environment.baseUrl;

  // private splashUrl = 'http://www.splashbase.co/api/v1/images/random';
  // private galleryUrl = 'http://localhost:3000/api/gallery';
  // private createScessionUrl = 'http://localhost:3000/api/createsession';
  // private getScessionUrl = 'http://localhost:3000/api/getsession';
  // private removeImageUrl = 'http://localhost:3000/api/deleteimage';
  // private updateImageUrl = 'http://localhost:3000/api/imageindex';

  private galleryUrl = this.baseUrl + 'session/view';
  private createScessionUrl = this.baseUrl + 'session/create';
  private getScessionUrl = this.baseUrl + 'session/listbycategory';
  private removeImageUrl = this.baseUrl + 'session/remove';
  private updateIndexUrl = this.baseUrl + 'session/updateIndex';

  constructor(private http: Http, private auth: AuthService) {
    this.headers.append('Authorization', this.auth.getAuthToken());
  }

  getImages(sessionId) {
    return this.http.post(this.galleryUrl, { sessionId: sessionId }, {headers: this.headers})
      .map(res => res.json());
  }

  removeImage(id) {
    return this.http.post(this.removeImageUrl, { fileId: id }, {headers: this.headers})
      .map(res => res.json());
  }

  updateImage(id, index) {
    return this.http.post(this.updateIndexUrl, { index: index, id: id }, {headers: this.headers})
      .map(res => res.json());
  }

  createSessions(sessionname, categoryname, categoryId) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let data = {
      sessionname: sessionname,
      categoryname: categoryname,
      categoryId: categoryId
     };
    return this.http.post(this.createScessionUrl, data, { headers: this.headers })
      .map(
      res => res.json(),
      err => console.log(err)
      );
  }

  getSessions(categoryId, isParent?) {
    // return this.visibleImages = IMAGES.slice(0);

    return this.http.post(this.getScessionUrl, {categoryId: categoryId, isParent: (isParent? isParent : false) }, {headers: this.headers})
      .map(res => res.json());
  }

  getAllSessions() {
    return this.http.get(this.baseUrl+'session/listSessions', {headers: this.headers})
      .map(res => res.json(),
          err => {
            console.log(err);
          });
  }

  // QR code && update delay implementation
  public generateQrUrl = this.baseUrl + 'session/generate-qr';
  public removeQrUrl = this.baseUrl + 'session/remove-codes';
  public checkQrUrl =  this.baseUrl + 'session/check-qr';
  public updateDelayUrl =  this.baseUrl + 'session/updateDelay';
  public updateTitleUrl = this.baseUrl + 'session/updateTitle';

  createQrCodes() {
    return this.http.get(this.generateQrUrl, {headers: this.headers}).map(res => res.json());
  }

  removeAllTokens() {
    return this.http.get(this.removeQrUrl, {headers: this.headers}).map(res => res.json());
  }

  checkQr () {
    return this.http.get(this.checkQrUrl, {headers: this.headers}).map(res => res.json());
  }

  updateDelay(imageId, delay) {
    return this.http.post(this.updateDelayUrl, {id: imageId, delay: delay}, {headers: this.headers}).map(res => res.json());
  }

  updateTitle (imageId, title) {
    return this.http.post(this.updateTitleUrl, {id: imageId, title: title}, {headers: this.headers}).map(res => res.json());
  }

}



