import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { FlashMessagesService } from 'angular2-flash-messages';

import { AuthService } from '../shared/auth.service';


@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  captchaKey: string = '6LevrjQUAAAAAM5WB0Xu_ttsNRqpXeSPV6F0_zek';

  registerForm: FormGroup = new FormGroup({
    username: new FormControl(null, Validators.required),
    email: new FormControl(null, Validators.required),
    password: new FormControl(null, Validators.required),
    name: new FormControl(null, Validators.required),
    captcha: new FormControl()
  });

  constructor(private authService: AuthService,
              private router: Router,
              private _flash: FlashMessagesService) { }

  ngOnInit() {
  }

  onRegister(){
    if(!this.registerForm.valid) return; //show error
    let credentials = {
      username: this.registerForm.value.username,
      email: this.registerForm.value.email,
      password: this.registerForm.value.password,
      name: this.registerForm.value.name
    };

    this.authService.registerUser(credentials)

    .subscribe(data => {
      if(data.success){
        this._flash.show(data.msg, { cssClass: 'alert-success', timeout: 3000 });
        this.router.navigate(['login']);
      } else {
        this._flash.show(data.msg, {cssClass: 'alert-danger', timeout: 3000});
      }
    }, err => {
      this._flash.show('Server Error', {cssClass: 'alert-danger', timeout: 3000});
    });
   }

}



